"use client";
import React, { FormEvent, useState } from "react";

const FloatingLabel = ({
  id,
  label,
  type = "text",
}: {
  id: string;
  label: string;
  type?: string;
}) => (
  <div className="relative mb-5">
    <input
      type={type}
      id={id}
      required
      placeholder=" "
      className="peer w-full px-4 py-4 text-base border-2 border-gray-200 rounded-xl bg-gray-50 focus:border-[#ff4b4b] focus:outline-none focus:ring-2 focus:ring-[#ff4b4b] focus:ring-opacity-10 transition-all"
    />
    <label
      htmlFor={id}
      className="absolute left-4 top-1/2 -translate-y-1/2 text-gray-600 transition-all peer-focus:-top-0.5 peer-focus:text-xs peer-focus:bg-white peer-focus:px-2 peer-[:not(:placeholder-shown)]:-top-0.5 peer-[:not(:placeholder-shown)]:text-xs peer-[:not(:placeholder-shown)]:bg-white peer-[:not(:placeholder-shown)]:px-2"
    >
      {label}
    </label>
  </div>
);

const Login = () => {
  const [isLoading, setIsLoading] = useState(false);

  const handleSubmit = (e: FormEvent) => {
    e.preventDefault();
    setIsLoading(true);
    setTimeout(() => {
      setIsLoading(false);
      alert("Login functionality would be implemented here");
    }, 1500);
  };

  return (
    <div className="min-h-screen flex bg-gradient-to-br from-[#f5f7fa] to-white">
      <div className="w-full max-w-15xl mx-auto p-5 flex gap-10 items-center justify-center">
        <div className="hidden md:block w-full max-w-lg">
          <div className="relative w-72 h-72 mx-auto">
            <div className="absolute w-24 h-24 bg-[#ff4b4b] rounded-tr-full rounded-bl-full rounded-br-full rotate-45 animate-pulse"></div>
          </div>
        </div>

        <div className="w-full max-w-2xl relative bg-white p-10 rounded-2xl shadow-lg overflow-hidden">
          <div className="absolute -top-12 -right-12 w-24 h-24 bg-[#ff4b4b] rounded-full opacity-10"></div>
          <div className="absolute -bottom-8 -left-8 w-16 h-16 bg-[#ff6b6b] rounded-full opacity-10"></div>

          <h1 className="text-2xl font-semibold text-gray-800 mb-8">
            Welcome to LifeFlow
          </h1>

          <form onSubmit={handleSubmit}>
            <FloatingLabel id="email" label="Email" type="email" />
            <FloatingLabel id="password" label="Password" type="password" />

            <button
              type="submit"
              disabled={isLoading}
              className="w-full py-4 bg-[#ff4b4b] text-white font-semibold rounded-xl hover:bg-[#ff3333] transform hover:-translate-y-0.5 transition-all disabled:opacity-75"
            >
              {isLoading ? "Signing in..." : "Sign In"}
            </button>

            <div className="mt-5 flex justify-between text-sm">
              <a
                href="#"
                className="text-[#ff4b4b] hover:text-[#ff3333] hover:underline transition-all"
              >
                Forgot password?
              </a>
              <a
                href="#"
                className="text-[#ff4b4b] hover:text-[#ff3333] hover:underline transition-all"
              >
                Create account
              </a>
            </div>
          </form>
        </div>
      </div>
    </div>
  );
};

export default Login;
